.side-menu-item {
    display: flex;
    padding: 16px 0;
    cursor: pointer;
    user-select: none;
    border-bottom: 1px solid rgba(0, 0, 0 , 0.12);

    &-icon {
        flex-shrink: 0;
        flex-basis: 16%;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 16%;
        height: 35px;
        fill: var(--main-color);
        padding: 0 8px;
    }

    .small-icon {
        height: 24px;
    }

    &-label {
        font-weight: 500;
        line-height: 1.3;
        flex-grow: 1;
        padding-left: 0.75rem;
        padding-right: 0.75rem;
        display: flex;
        align-items: center;
    }

    &-arrow {
        padding-left: 0.75rem;
        padding-right: 0.75rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1rem;
        transform: scale(0.6, 0.6);
        opacity: 0.6;
    }
}

.small-item {
    padding: 14px 0;

    .side-menu-item-icon {
        width: 16%;
        height: 28px;
        fill: color-mix(in srgb, var(--main-color) 84%, rgba(255,255,255,0.5));
    }

    .small-icon {
        height: 24px;
    }
}