.catalog {
  max-width: 1200px;
  padding: 20px 10px;


  .breadcrumb {
    list-style: none;
    font-size: 14px;
    line-height: 1.29;

    &-item {
      display: inline-block;

      &-separator {
        padding: 0 8px;
      }

      &-current {
        color: var(--font-color-disabled);
      }

      &-link {
        text-decoration: none;
        color: var(--font-color);
      }
    }
  }

  .page-header {
    font-size: 28px;
    margin-bottom: 8px;
  }

  &-grid {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  }
}