.catalog {
  max-width: 1200px;
  padding: 20px 10px;
}
.catalog .breadcrumb {
  list-style: none;
  font-size: 14px;
  line-height: 1.29;
}
.catalog .breadcrumb-item {
  display: inline-block;
}
.catalog .breadcrumb-item-separator {
  padding: 0 8px;
}
.catalog .breadcrumb-item-current {
  color: var(--font-color-disabled);
}
.catalog .breadcrumb-item-link {
  text-decoration: none;
  color: var(--font-color);
}
.catalog .page-header {
  font-size: 28px;
  margin-bottom: 8px;
}
.catalog-grid {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
}

