.bottom-menu {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  background-color: #ffffff;
  box-shadow: 0 30px 10px 30px rgba(0, 0, 0 , 0.12);
  z-index: 1000;
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;

  &-item {
    width: 52px;
    height: 50px;
    padding: 12px;
    cursor: pointer;
    user-select: none;
    color: #6d6d6d;
    position: relative;
  }
}