.side-menu {
    position: absolute;
    z-index: 1000;
    width: 100vw;
    min-height: calc(100dvh - 55px);
    background-color: white;
    top: 55px;
    left: -100vw;
    transition: left 0.3s ease-out;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 50px;

    &-open {
        left: 0;
    }

    &-catalog {
        width: 100%;
        border-top: 1px solid rgba(0, 0, 0, 0.12);
    }

    .side-menu-bottom {
        width: 100%;
    }
}